@import '../../styles/common.css';

.container {
  width: 100%;
  height: 100vh;
  background-color: #fff;
}

.copyLayout {
  max-width: 700px;
  display: flex;
  flex-direction: column;

  @media (max-height: 500px) {
    flex-direction: row;
  }

  @media (max-width: 500px) and (max-height: 950px) {
    flex-direction: column;
  }
}

.resume,
.hireme {
  flex: 1;
}

.background,
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.background {
  height: 50%;
}

.bullet,
.download,
.star {
  display: inline-block;
  font-size: 75%;
  margin-right: 0.25rem;
  vertical-align: middle;
}

.download {
  margin-top: -0.2rem;
  transform: rotateZ(180deg);
}

.star {
  margin-top: -0.25rem;
}

.companyName {
  cursor: pointer;
  outline: none;
}

.content {
  max-width: 1200px;
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: end;
  margin: auto;
  width: 75%;
  @media (max-height: 900px) {
    font-size: 90%;
    line-height: 1.75;
  }
  @media (max-width: 500px) and (max-height: 950px) {
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
  }
}

.contentPadding {
  position: relative;
  width: 100%;
  height: 66%;
  @media (max-height: 750px) {
    width: 100%;
    height: 100%;
    height: 75%;
  }
}

ul > li {
  @media (max-width: 500px) and (max-height: 950px) {
    border: 1px solid #000000;
    padding: 0.25rem 0.5rem;
    box-sizing: border-box;
    margin-bottom: 0.25rem;
  }
}

.foreground {
  z-index: 2;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  @media (max-height: 700px) {
    margin-top: -10%;
  }
  @media (max-height: 500px) {
    margin-top: 5%;
  }
}

.header1 {
  font-size: 3rem;
  @media (max-width: 450px) {
    font-size: 2rem;
  }
}

.header2 {
  font-size: 1.5rem;
  @media (max-width: 450px) {
    font-size: 0.75rem;
    font-weight: bold;
    line-height: 1.5;
  }
}
